@import "../../styles/helpers/variables"
@import "../../styles/helpers/mixins"

.figure-wrapper
    position: absolute
    top: 5%
    left: 0%
    right: 0%
    bottom: 0%
    height: 70vh
    z-index: 1
    stroke-width: 2px
.work-section
    width: 80vw
    margin: 100px auto
    color: $secondaryColorBackground
    h2
        font-size: 65px
        margin-bottom: 50px

    &.black
        @include centerFlex($direction: row,$justifyContent: none )

        position: relative
        padding: 10%

        column-gap: 2%
        margin-bottom: -5px

        width: 100vw
        height: 150vh
        transition: background-color 0.5s ease-in-out

        .large-projects
            align-self: flex-start

        .list-item
            color: inherit

            border: 1.5px solid $secondaryColorBackground

            a
                color: inherit
                border: 1.5px solid $mainColorBackground
                background: none

                &:hover
                    color: $secondaryColorBackground
                    background-color: $colorSecondary

        &.is-reveal
            background-color: $secondaryColorBackground
            color: $mainColorBackground
            .list-item
                border: 1.5px solid $mainColorBackground

        @media screen and (max-width: 450px)
            padding: 1%
            height: auto
    @media screen and (max-width: 450px)
        width: 100vw

.header-workpage

    @include centerFlex($direction:  row,$justifyContent: center )
    column-gap: 20px
    row-gap: 50px

    h1
        position: relative
        font-size: clamp(1vw,85px,15vw)

    .projects-wrapper
        display: flex
        flex-direction: column
        align-items: center
        justify-content: center

    .windows-wrapper
        display: flex
        flex-direction: row
        justify-content: center
        align-items: center
        width: 50vw
        margin: 10% auto
        opacity: 1
        position: relative

        @media screen and (max-width: 450px)
            margin: 8% 0
            width: 90vw

        img
            padding: 2%
            height: auto

        .circle-background

            position: absolute
            width: 35vw
            height: 35vw
            border-radius: 50%
            background-color: #849AD8
            z-index: -1
            @media screen and (max-width: 450px)
                width: 90vw
                height: 90vw
